import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Stepper from "react-stepper-horizontal";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import Skeleton from "react-loading-skeleton";
import Axios from "axios";
import SelectSearch from "react-select-search";
import {
  getValidasiCustomerPembayaranPerbon,
  getValidasiCustomerPenjualan,
  getValidasiCustomerReturnPenjualan,
  getValidasiCustomerTitipGrCustomer,
  getValidasiCustomerTolakPembayaranCustomer,
} from "../actions/validasiCustomerAction";
import { addFaktur, checkReplikasiValidasi, deleteFaktur } from "../FindIndex";
import Swal from "sweetalert2";
import { berhasil, gagal } from "../Alert";
import { getDataSelectedValidasiSupplier } from "../actions/validasiSupplierAction";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const maptoState = (state) => {
  return {
    listValidasiCustomer: state.provinsi.listValidasiCustomer,
    listDataSelectedValidasiSupplier:
      state.provinsi.listDataSelectedValidasiSupplier,
  };
};
const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class validasiCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
        },
        {
          dataField: "namaAktor",
          text: "Nama Aktor",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "rp",
          text: "Ongkos(RP)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            var hasil = checkReplikasiValidasi("FakturTerpilih", row.noFaktur);
            if (hasil === -1) {
              return (
                <div className="text-center">
                  <button
                    className="btn btn-warning mr-2"
                    onClick={(e) =>
                      addFaktur(
                        row.noFaktur,
                        row.tanggal,
                        row.noBon,
                        row.supplier,
                        row.bruto,
                        row.netto,
                        row.bonRp,
                        row.titipRp,
                        row.titipGr,
                        row.keterangan,
                        Swal.fire({
                          position: "top-right",
                          icon: "success",
                          text: "Ditambahkan ke List",
                          showConfirmButton: false,
                          timer: 1500,
                        }),
                        this.disabledButton(e)
                      )
                    }
                  >
                    <i className="fa fa-check-circle mr-1"></i>
                    Pilih
                  </button>
                </div>
              );
            } else {
              return (
                <div>
                  <button
                    className="btn btn-warning mr-2"
                    onClick={() =>
                      deleteFaktur(
                        row.noFaktur,
                        this.updateButton(this.state.tipeTransaksi)
                      )
                    }
                  >
                    <i className="fa fa-times mr-1"></i>
                    Batal
                  </button>
                </div>
              );
            }
          },
        },
      ],
      data: [
        {
          tanggal: "23/11/2020",
        },
      ],
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      listSales: [],
      listCustomer: [],
    };
  }
  updateButton(data) {
    switch (data) {
      case "PENJUALAN":
        this.props.dispatch(getValidasiCustomerPenjualan());
        break;
      case "RETURN PENJUALAN":
        this.props.dispatch(getValidasiCustomerReturnPenjualan());
        break;
      case "PEMBAYARAN PER BON":
        this.props.dispatch(getValidasiCustomerPembayaranPerbon());
        break;
      case "TOLAKAN PEMBAYARAN CUSTOMER":
        this.props.dispatch(getValidasiCustomerTolakPembayaranCustomer());
        break;
      case "TITIP GR CUSTOMER":
        this.props.dispatch(getValidasiCustomerTitipGrCustomer());
        break;
      default:
        console.log("ERROR");
        break;
    }
  }
  kirimvalidasi(data) {
    console.log("BERHASIL");
    var hasil = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
    switch (data) {
      case "PENJUALAN":
        Axios.put(serverNonMaster + "cst/dc/pjl", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiCustomerPenjualan()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "RETURN PENJUALAN":
        Axios.put(serverNonMaster + "cst/dc/rtr", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiCustomerReturnPenjualan()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "PEMBAYARAN PER BON":
        Axios.put(serverNonMaster + "cst/dc/byr", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() =>
            this.props.dispatch(getValidasiCustomerPembayaranPerbon())
          )
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "TOLAKAN PEMBAYARAN CUSTOMER":
        Axios.put(serverNonMaster + "cst/dc/tlkbyr", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() =>
            this.props.dispatch(getValidasiCustomerTolakPembayaranCustomer())
          )
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "TITIP GR CUSTOMER":
        Axios.put(serverNonMaster + "cst/dc/ttp", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiCustomerTitipGrCustomer()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      default:
        console.log("ERROR");
        break;
    }
  }
  disabledButton = (e) => {
    e.target.disabled = true;
  };
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    }
    this.props.dispatch(getDataSelectedValidasiSupplier());
  };
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    }
  };
  changeTransaksi = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
    switch (e.target.value) {
      case "PENJUALAN":
        this.props.dispatch(getValidasiCustomerPenjualan());
        break;
      case "RETURN PENJUALAN":
        this.props.dispatch(getValidasiCustomerReturnPenjualan());
        break;
      case "PEMBAYARAN PER BON":
        this.props.dispatch(getValidasiCustomerPembayaranPerbon());
        break;
      case "TOLAKAN PEMBAYARAN CUSTOMER":
        this.props.dispatch(getValidasiCustomerTolakPembayaranCustomer());
        break;
      case "TITIP GR CUSTOMER":
        this.props.dispatch(getValidasiCustomerTitipGrCustomer());
        break;
      default:
        console.log("ERROR");
        break;
    }
  };

  componentDidMount() {
    Axios.get(server + "storages2?tipeData=SALES").then((res) =>
      this.setState({
        listSales: res.data,
      })
    );
    Axios.get(server + "customers").then((res) =>
      this.setState({
        listCustomer: res.data,
      })
    );
  }
  // ONCHANGE PILIH KOTA
  onSubmitToko(data) {
    this.props.change("namaToko", data);
  }
  render() {
    var namaToko = this.state.listCustomer.map((obj) => {
      var payload = {
        value: obj.kodeCustomer,
        name: obj.namaToko,
      };
      return payload;
    });
    var sales = this.state.listSales.map((obj) => {
      var payload = {
        value: obj.kodeLokasi,
        name: obj.namaLokasi,
      };
      return payload;
    });
    return (
      <div className="container-fluid">
        <div className="card">
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <Stepper
                    steps={[
                      { title: "Pilih Data" },
                      { title: "Validasi Data" },
                      // { title: "Data Rekening" },
                    ]}
                    activeStep={this.state.step}
                  />
                </div>
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-3 mb-3">
                      <label htmlFor="">Jenis Transaksi</label>
                      <Field
                        name="tipeTransaksi"
                        label="Dari"
                        component="select"
                        className="form-control"
                        onChange={this.changeTransaksi("tipeTransaksi")}
                      >
                        <option value="DEFAULT" key="DEFAULT">
                          SILAHKAN PILIH
                        </option>
                        <option value="PENJUALAN" key="PENJUALAN">
                          PENJUALAN
                        </option>
                        <option value="RETURN PENJUALAN" key="RETURN PENJUALAN">
                          RETURN PENJUALAN
                        </option>
                        <option
                          value="PEMBAYARAN PER BON"
                          key="PEMBAYARAN PER BON"
                        >
                          PEMBAYARAN PER BON
                        </option>
                        <option
                          value="TOLAKAN PEMBAYARAN CUSTOMER"
                          key="TOLAKAN PEMBAYARAN CUSTOMER"
                        >
                          TOLAKAN PEMBAYARAN CUSTOMER
                        </option>
                        <option
                          value="TITIP GR CUSTOMER"
                          key="TITIP GR CUSTOMER"
                        >
                          TITIP GR CUSTOMER
                        </option>
                      </Field>
                    </div>
                    <div className="col-lg-9"></div>
                    <div className="col-lg-3">
                      <label htmlFor="">Nama Toko</label>
                      <SelectSearch
                        options={sales}
                        search
                        placeholder="Silahkan Pilih"
                        onChange={(data) => this.props.change("sales", data)}
                      />
                    </div>
                    <div className="col-lg-3 d-none">
                      <Field
                        name="sales"
                        label="Nama Toko"
                        type="text"
                        component={renderField}
                      ></Field>
                    </div>
                    <div className="col-lg-3">
                      <label htmlFor="">Nama Toko</label>
                      <SelectSearch
                        options={namaToko}
                        search
                        placeholder="Silahkan Pilih"
                        onChange={(data) => this.props.change("namaToko", data)}
                      />
                    </div>
                    <div className="col-lg-3 d-none">
                      <Field
                        name="namaToko"
                        label="Nama Toko"
                        type="text"
                        component={renderField}
                      ></Field>
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="tanggal"
                        label="Tanggal Transaksi"
                        component={renderField}
                        type="date"
                      />
                    </div>
                    <div className="col-lg-3">
                      <label htmlFor="">Tipe Transaksi</label>
                      <Field
                        name="Tipe Transaksi"
                        label="Tanggal Transaksi"
                        component="select"
                        className="form-control"
                      >
                        <option
                          value="
                            DEFAULT"
                          key="
                            DEFAULT"
                        >
                          Silahkan PIlih
                        </option>
                        <option value="UMUM" key="UMUM">
                          UMUM
                        </option>
                        <option value="FICO" key="FICO">
                          FICO
                        </option>
                      </Field>
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button className="btn btn-primary">
                          <i className="fa fa-eye"></i> Lihat Data
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12 mt-3">
                      {this.props.listValidasiCustomer ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.props.listValidasiCustomer}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <Skeleton
                          width={"100%"}
                          height={200}
                          count={1}
                          className="mb-1"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.nextStep()}
                        >
                          Next
                          <i className="fa fa-chevron-right ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className={this.state.step2}>
                    <div className="col-lg-12 mt-3">
                      {this.props.listDataSelectedValidasiSupplier ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.props.listDataSelectedValidasiSupplier}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <Skeleton
                          width={"100%"}
                          height={50}
                          count={5}
                          className="mb-1"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() =>
                            this.kirimvalidasi(this.state.tipeTransaksi)
                          }
                        >
                          <i className="fa fa-check mr-3"></i>
                          VALIDASI
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

validasiCustomer = reduxForm({
  form: "validasiCustomer",
  enableReinitialize: true,
})(validasiCustomer);
export default connect(maptoState, null)(validasiCustomer);
